#root {
  height: 100vh;
  width: 100vw;
}

.box {
  margin: 0 auto 0 auto;
  padding: 2em;
  max-height: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 20px;
}

.gif {
  max-height: 300px;
  max-width: 500px;
  border-radius: 20px;
}

.button {
  border: rgba(255, 255, 255, 0.8) solid 1px;
  background-color: rgba(255, 255, 255, 0.5);
}

.box {
  border: #FFFFFF80 solid 2px;
}

.h1, body {
  margin: 0;
}

.background{
  height: 100%;
  width: 100%;
}

.red {
  background-color: #ff4343;
  color: #FFFFFF80;
}

.orange {
  background-color: #ffa143;
  color: #FFFFFF80;
}

.yellow {
  background-color: #ffff43;
  color: #00000080;

  .box {
    border: #00000080 solid 2px;
  }

  .button {
    border: #00000020 solid 1px;
    background-color: #00000008;
  }
}

.green {
  background-color: #a1ff43;
  color: #FFFFFF80;
}

.blue {
  background-color: #43a1ff;
  color: #FFFFFF80;
}

.purple {
  background-color: #c06afd;
  color: #FFFFFF80;
}

.black {
  background-color: black;
  color: #FFFFFF80;
}

.grey, .gray {
  background-color: gray;
  color: #FFFFFF80;
}

.white {
  background-color: white;
  color: #00000080;

  .box {
    border: #00000080 solid 2px;
  }
  
  .button {
    border: #00000020 solid 1px;
    background-color: #00000008;
  }
}
